import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input } from 'antd';
import { getSubjectEduById, createSubjectEdu, updateSubjectEdu } from 'services/subjectEdu';
import { getQueryParams } from 'utils/urlUtil';
import { getSubjectGroups } from 'services/subjectGroup';
import { getKnowledges } from 'services/knowledgeUnit';
import { getSubjectTypes } from 'services/subjectType';
import { SelectAjax } from 'components/common';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
    subjectGroupId: '',
    knowledgeId: 0,
    subjectTypeId: 0,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormSubjectEduPage' })
class FormSubjectEduPage extends Component {
  state = {
    dataId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.setState({ dataId: queryParamId });
      this.getDataById(queryParamId);
    }
  };

  getDataById = async id => {
    const res = await getSubjectEduById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { dataId } = this.state;
    const payload = {
      id: values.id,
      label: values.label,
      subjectGroupId: values.subjectGroupId,
      knowledgeId: parseInt(values.knowledgeId),
      subjectTypeId: parseInt(values.subjectTypeId),
    };
    try {
      if (!dataId) {
        await createSubjectEdu(payload);
      } else {
        await updateSubjectEdu(payload);
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { dataId } = this.state;
    return (
      <Card title={`${dataId ? 'Cập nhật' : 'Thêm'} tòa nhà KTX`}>
        <Row>
          <Form onSubmit={this.handleSubmit}>
            <Form.Item hasFeedback label="Mã" {...formItemLayout}>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={dataId !== false} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Bộ môn" hasFeedback>
              {getFieldDecorator('subjectGroupId', {
                initialValue: initialValue.subjectGroupId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập Nhóm môn!',
                  },
                ],
              })(<SelectAjax service={getSubjectGroups} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khối kiến thức" hasFeedback>
              {getFieldDecorator('knowledgeId', {
                initialValue: initialValue.knowledgeId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập Khối kiến thức!',
                  },
                ],
              })(<SelectAjax service={getKnowledges} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Loại môn" hasFeedback>
              {getFieldDecorator('subjectTypeId', {
                initialValue: initialValue.subjectTypeId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập Khối kiến thức!',
                  },
                ],
              })(<SelectAjax service={getSubjectTypes} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSubjectEduPage {...props} />
  </Page>
));



