import { request } from 'utils';

const prefix = '/api/categories';

export const getSubjectEdu = ({
  label,
  noOfFloor,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Subject',
    method: 'GET',
    params: { label, noOfFloor, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getSubjectEduById = id =>
  request({
    prefix,
    url: `/Subject/GetById`,
    params: { id },
    method: 'GET',
  });

export const deleteSubjectEdu = id =>
  request({
    prefix,
    url: `/Subject`,
    data: { id },
    method: 'DELETE',
  });

export const updateSubjectEdu = ({ id, label, subjectGroupId, knowledgeId, subjectTypeId }) =>
  request({
    prefix,
    url: `/Subject`,
    data: { id, label, subjectGroupId, knowledgeId, subjectTypeId },
    method: 'PUT',
  });

export const createSubjectEdu = ({ id, label, subjectGroupId, knowledgeId, subjectTypeId }) =>
  request({
    prefix,
    url: `/Subject`,
    data: { id, label, subjectGroupId, knowledgeId, subjectTypeId },
    method: 'POST',
  });



